@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap);
.header {
  margin: 4.375rem 0 3.125rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

h1 {
  color: hsl(0, 0%, 98%);
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 1.25rem;
}

.header_btn {
  background: none;
  border: none;
}

.btn,
.btn-sbm {
  padding: 0.625rem;
  margin-right: 1.563rem;
  background: transparent;
  box-shadow: 2px 1000px 1px var(--background) inset;
  border: 1px solid var(--border-grayish-blue);
  border-radius: 50%;
  cursor: pointer;
  transition: hover 0.2s;
}

.btn:hover {
  border: 1px solid transparent;
  background-image: linear-gradient(hsl(192, 100%, 67%), hsl(280, 87%, 65%));
}

.btn,
.btn-sbm:focus {
  outline: none;
}

.btn:focus {
  background-image: url(/TODO_APP/static/media/icon-check.5742160c.svg),
    linear-gradient(135deg, hsl(192, 100%, 67%), hsl(280, 87%, 65%));
  border: none;
  box-shadow: none;
  background-repeat: no-repeat;
  background-position: center;
}

.todo_container {
  padding: 1.3rem 1.25rem;
  margin-bottom: 1.563rem;
  background-color: var(--background);
  border-radius: 5px;

  display: flex;
  align-items: flex-end;
}

input {
  color: var(--font-color);
  font-family: inherit;
  font-size: inherit;
  width: 90%;

  /* line-height??? */
  border: none;
  background-color: var(--background);
}

input:focus {
  outline: none;
}

::-webkit-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: var(--dark-grayish-blue);
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: var(--dark-grayish-blue);
  opacity: 1; /* Firefox */
}

::placeholder {
  /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: var(--dark-grayish-blue);
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder {
  /* Internet Explorer 10-11 */
  color: var(--dark-grayish-blue);
}

input:focus::-webkit-input-placeholder {
  color: transparent;
}

input:focus:-ms-input-placeholder {
  color: transparent;
}

input:focus::placeholder {
  color: transparent;
}

.todo {
  padding: 1.3rem 1.25rem;
  border-bottom: 1px solid var(--dark-grayish-blue);

  display: flex;
  align-items: center;
}

.todo_item {
  align-self: flex-end;
}

.todo .trash-btn {
  display: none;
  border: none;
  background: none;
  cursor: pointer;
}

.todo:hover {
  cursor: pointer;
}

.todo:hover .trash-btn {
  display: block;
  margin-left: auto;
}

.drag {
  box-shadow: 0 0 15px black;
}

input[type='checkbox'] {
  -webkit-appearance: none;
  appearance: none;

  height: 22px;
  width: 22px;
  padding: 0.625rem;
  margin-right: 1.563rem;
  background: transparent;
  border: 1px solid var(--border-grayish-blue);
  border-radius: 50%;
  box-shadow: 2px 1000px 1px var(--background) inset;
  cursor: pointer;
  transition: hover 0.2s;
}

input[type='checkbox']:hover {
  border: 1px solid transparent;
  background-image: linear-gradient(
    135deg,
    hsl(192, 100%, 67%),
    hsl(280, 87%, 65%)
  );
}

input[type='checkbox']:checked {
  background-image: url(/TODO_APP/static/media/icon-check.5742160c.svg),
    linear-gradient(135deg, hsl(192, 100%, 67%), hsl(280, 87%, 65%));
  background-repeat: no-repeat;
  background-position: center;
  border: none;
  box-shadow: none;
}

label {
  cursor: pointer;
}

input[type='checkbox'].checked:focus,
label li.completed {
  color: var(--dark-grayish-blue);
  text-decoration: line-through;
}

.list {
  font-size: 0.875rem;
  color: var(--dark-grayish-blue);
  padding: 1.25rem;

  display: flex;
  justify-content: space-between;
  align-items: center;
}

.list_btns {
  font-family: inherit;
  font-size: inherit;
  font-weight: 700;
  color: var(--dark-grayish-blue);
  border: none;
  background-color: inherit;
  cursor: pointer;
}

.list_btn--active {
  padding: 0 1.25rem;
}

.list_btn--clear {
  font-weight: normal;
}

.list_btn--active:hover,
.list_btn--completed:hover,
.list_btn--clear:hover {
  color: var(--font-color);
}

.list_btn--all {
  color: hsl(220, 98%, 61%);
}

.todos_list {
  color: var(--font-color);
  background-color: var(--background);
  border-radius: 5px;
  box-shadow: 0 2px 8px rgba(32, 32, 32, 0.003);

  display: flex;
  flex-direction: column;
}

.todo_list {
  list-style: none;
}



/* Primary

- Bright Blue: hsl(220, 98%, 61%)
- Check Background: linear-gradient hsl(192, 100%, 67%) to hsl(280, 87%, 65%)

### Neutral

### Light Theme

- Very Light Gray: hsl(0, 0%, 98%)
- Very Light Grayish Blue: hsl(236, 33%, 92%)
- Light Grayish Blue: hsl(233, 11%, 84%)
- Dark Grayish Blue: hsl(236, 9%, 61%)
- Very Dark Grayish Blue: hsl(235, 19%, 35%)

### Dark Theme

- Very Dark Blue: hsl(235, 21%, 11%)
- Very Dark Desaturated Blue: hsl(235, 24%, 19%)
- Light Grayish Blue: hsl(234, 39%, 85%)
- Light Grayish Blue (hover): hsl(236, 33%, 92%)
- Dark Grayish Blue: hsl(234, 11%, 52%)
- Very Dark Grayish Blue: hsl(233, 14%, 35%)
- Very Dark Grayish Blue: hsl(237, 14%, 26%)

## Typography

### Body Copy

- Font size: 18px

### Font

- Family: [Josefin Sans](https://fonts.google.com/specimen/Josefin+Sans)
- Weights: 400, 700 */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
:root {
  --background: hsl(0, 0%, 98%);
  --bg-main: hsl(236, 33%, 92%);

  --dark-grayish-blue: hsl(233, 11%, 84%);
  --font-color: hsl(235, 19%, 35%);
  --border-grayish-blue: hsl(236, 33%, 92%);
  --background-image: url(/TODO_APP/static/media/bg-desktop-light.988cb74c.jpg);
}

[data-theme='dark'] {
  --bg-main: hsl(235, 21%, 11%);
  --background: hsl(235, 24%, 19%);

  --font-color: hsl(234, 39%, 85%);

  --dark-grayish-blue: hsl(234, 11%, 52%);
  --border-grayish-blue: hsl(233, 14%, 35%);
  --background-image: url(/TODO_APP/static/media/bg-desktop-dark.02105f02.jpg);
}

body {
  font-family: 'Josefin Sans', sans-serif;
  font-weight: 400;
  font-size: 1.125rem;
}

.container {
  min-height: 100vh;
  background-image: url(/TODO_APP/static/media/bg-desktop-light.988cb74c.jpg);
  background-image: var(--background-image);
  background-repeat: no-repeat;
  background-color: hsl(236, 33%, 92%);
  background-color: var(--bg-main);
}
.main {
  width: 33.75rem;
  max-width: 90%;
  margin: 0 auto;
  color: hsl(235, 19%, 35%);
  color: var(--font-color);

  display: flex;
  flex-direction: column;
  justify-content: center;
}


